
.header {
  height: 55vh;
  background-image: url(/Oveluna2.0/static/media/header_bg.15bd4c79.JPG);
  font-family: "Mali", sans-serif;
  background-size:cover; 
  position: relative;
}

.header h3 {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 1.40em;
  margin: 0;
}

header nav {
  float: right;
  line-height: 100px;
}

header nav a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  padding-top: 30px;
  padding-right: 30px;
  padding-bottom: 30px;
  padding-left: 30px;
  line-height: normal;
  transition: all 600ms ease;
}

header nav a:hover {
  background: #d7bde2;
  border-radius: 40px;
  text-decoration: none;
  color: #fff;
}

.header-content {
  position: absolute;
  z-index: 1;
  top: 10%;
  left: 50%;
  margin-top: 100px;
  margin-left: -145px;
  width: 300px;
  height: 100px;
  text-align: center;
  color: #fff;
}

